import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../components/home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: home
  },
  {
    path: '/signIn',
    name: 'SignIn',
    component: () => import('../components/signIn.vue')
  },
  {
    path: '/partner',
    name: 'Partner',
    component: () => import('../components/partner.vue')
  },
  {
    path: '/product',
    name: 'Product',
    component: () => import('../components/product.vue')
  },
  {
    path: '/bonus',
    name: 'Bonus',
    component: () => import('../components/bonus.vue')
  },
  {
    path: '/regulations',
    name: 'Regulations',
    component: () => import('../components/regulations.vue')
  },
  {
    path: '/contactUs',
    name: 'ContactUs',
    component: () => import('../components/contactUs.vue')
  },
  {
    path: '*',
    name: 'NotFound',
    component: home
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
