import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueScrollTo from 'vue-scrollto'

Vue.use(VueScrollTo)
Vue.config.productionTip = false

function localJason () {
  fetch('jasonData.json').then(res => { return res.json() }).then(json => {
    store.commit('setJsonData', json)
    router.push('/')
  })
}

router.beforeEach((to, from, next) => {
  if (!store.state.pageData) {
    setTimeout(() => {
      localJason()
    }, 1250)
  } else {
    document.documentElement.scrollTop = 0
    next()
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
