<template>
  <header class="header">
    <h1>
      <router-link to="/" v-scroll-to="{ el: '.banner', offset: -50 }">
        <!-- <i class="fa fa-wave-square" /> -->{{ $store.state.pageData.companyName }}
      </router-link>
    </h1>
    <div>
      <router-link v-for="(el, i) in calcNavigation" :key="i"
        :target="el.target"
        :to="el.to">
        {{ el.name }}
      </router-link>
      <a :href="$store.state.pageData.mamberLink.url" target="_blank">üßë{{ $store.state.pageData.mamberLink.name }}</a>
    </div>
    <!-- <nav>
      <ul>
        <li class="special" @click="menuHandler">
          <a class="menuToggle"><span>menu</span></a>
          <div class="navMenu">
            <ul>
              <a class="close"></a>
              <li v-for="(el, i) in calcNavigation" :key="i">
                <router-link :to="el.to" :target="el.target">{{ el.name }}</router-link>
              </li>
              <li><a :href="$store.state.pageData.mamberLink.url" target="_blank">{{ $store.state.pageData.mamberLink.name }}</a></li>
            </ul>
          </div>
        </li>
      </ul>
    </nav> -->
  </header>
</template>

<script>
export default {
  name: 'navBar',
  computed: {
    calcNavigation () {
      return this.$store.state.pageData.navigation.filter(el => el.visiable)
    }
  },
  methods: {
    // Nav Menu Toggle
    menuHandler () {
      const bodyEl = document.body
      !bodyEl.className
        ? bodyEl.classList.add('is-menu-visible')
        : bodyEl.classList.remove('is-menu-visible')
    }
  }
}
</script>

<style lang="scss">
.navMenu {
  transform: translateX(20em);
  transition: transform 0.5s ease;
  -webkit-overflow-scrolling: touch;
  background: #21b2a6;
  color: #ffffff;
  height: 100%;
  max-width: 80%;
  overflow-y: auto;
  padding: 3em 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 20em;
  z-index: 10002;
  ul {
    list-style: none;
    padding: 0;
    li {
      border-top: solid 1px rgba(0, 0, 0, 0.125);
      padding: 0 2em;
      a {
        border: 0;
        color: inherit;
        display: block;
        font-size: 0.8em;
        letter-spacing: 0.225em;
        outline: 0;
        text-decoration: none;
        text-transform: uppercase;
      }
      &:hover {
        background-color: rgba(0,0,0,.12);
      }
      &:first-child {
        border-top: 0;
        margin-top: 0;
        padding-top: 0;
      }
    }
  }
}
</style>
